export default {
  name: 'Home',
  data () {
    return {
      db: window.gtvapp.db
    }
  },
  methods: {},
  created () {
    // INIT DB IF NEEDED
    // const idb = window.indexedDB
    // console.log('opendb')
    // let open = idb.open('gtvappdb', 1)
    // open.onupgradeneeded = () => {
    //   console.log('onupgradeneeded')
    //   let db = open.result
    //   let store = db.createObjectStore('Categories', {keyPath: '_id'})
    //   store.createIndex('NameIndex', ['name'])
    //   store = db.createObjectStore('DocSets', {keyPath: '_id'})
    //   store.createIndex('TitleIndex', ['title'])
    //   store = db.createObjectStore('Pdf', {keyPath: '_id'})
    //   store.createIndex('TitlePdfIndex', ['title'])
    // }
    // SQLITE INIT
    const sqldb = window.openDatabase("gtvappdb", "1.0", "GTVAPP database", 1000000); //will create database Dummy_DB or open it
    sqldb.transaction(
      (tx) => {
        tx.executeSql('CREATE TABLE IF NOT EXISTS Categories (_id INT PRIMARY KEY,data TEXT NOT NULL)')
        tx.executeSql('CREATE TABLE IF NOT EXISTS DocSets (_id TEXT PRIMARY KEY,data TEXT NOT NULL)')
        tx.executeSql('CREATE TABLE IF NOT EXISTS Pdf (_id TEXT PRIMARY KEY,data TEXT NOT NULL)')
      },
      (err) => { console.log('SQL ERROR : ' + err.code) },
      () => { console.log('SQL SUCCESS') }
    )
    // GET LAST NUM_EXPORT REQUESTED
    let lastNumExport = this.$ls.get('num_export')
    console.log(lastNumExport)
    if(lastNumExport === undefined || lastNumExport === null) lastNumExport = 0
    // =========================================================================
    // Try to get datas (categories & docsets)
    window.gtvapp.wait.c++
    window.gtvapp.wait.msg = 'Chargement du contenu...'
    console.log('GET')
    console.log(window.gtvapp.categories !== undefined ? window.gtvapp.categories.length : '0')
    console.log(window.gtvapp.docsets !== undefined ? window.gtvapp.docsets.length : '0')
    if (
      window.gtvapp.categories !== undefined &&
      window.gtvapp.categories.length > 0 &&
      window.gtvapp.docsets !== undefined &&
      window.gtvapp.docsets.length > 0
    ) {
      this.$http.get(
        // `${window.gtvapp.interfaceUrl}/Descriptif/${window.gtvapp.me.login}/${window.gtvapp.id_mobile}/189800`/*,
        `${window.gtvapp.interfaceUrl}/Descriptif/${window.gtvapp.me.login}/${window.gtvapp.id_mobile}/${lastNumExport}`/*,
        {headers:window.gtvapp.interfaceOptions}*/
      ).then(
        async res => {
          // Connected : Get the last data according to num_export
          let categories = res.body.data.json.categories.data/* .slice(10, 20) */
          let docsets = res.body.data.json.docsets.data
          // console.log({categories, docsets})
          // =====================================================================
          // Update / Create categories in localDB
          console.log('put categories in db')
          window.gtvapp.wait.msg = 'Synchronisation des categories...'
          categories.forEach(async (c) => {
            // console.log(c._id)
            let category = await this.db.getSQL('Categories', c._id)
            // let category = await this.db.get('Categories', c._id)
            if(category === undefined){
              await this.db.putSQL('Categories', c)
              // await this.db.put('Categories', c)
            }
          })
          // GetAll Categories
          window.gtvapp.categories = await this.db.getSQL('Categories')
          // console.log(JSON.stringify(window.gtvapp.categories))
          // window.gtvapp.categories = await this.db.get('Categories')
          // =====================================================================
          // Update / Create docsets in localDB
          console.log('put docsets in db')
          window.gtvapp.wait.msg = 'Synchronisation des documents...'
          docsets.forEach(async (d) => {
            // console.log(d._id)
            lastNumExport = Math.max(lastNumExport, d.num_export)
            let docset = await this.db.getSQL('DocSets', d._id)
            // let docset = await this.db.get('DocSets', d._id)
            if(docset === undefined){
              await this.db.putSQL('DocSets', d)
              // await this.db.put('DocSets', d)
            }
          })
          console.log(lastNumExport)
          this.$ls.set('num_export', lastNumExport)
          // GetAll DocSets
          window.gtvapp.docsets = await this.db.getSQL('DocSets')
          // console.log(JSON.stringify(window.gtvapp.docsets))
          // window.gtvapp.docsets = await this.db.get('DocSets')
          window.gtvapp.wait.c--
          this.$forceUpdate();
          // Switch to /category vue
          this.$router.push('/category')
        },
        async err => {
          // Disconnected : Get the last data from local DB
          window.gtvapp.wait.c--
          window.gtvapp.messages.push({type:'info', message: `Fonctionnement hors connexion`})
          setInterval(()=>{window.gtvapp.messages.pop()}, 5000)
          window.gtvapp.categories = await this.db.getSQL('Categories')
          // window.gtvapp.categories = await this.db.get('Categories')
          window.gtvapp.docsets = await this.db.getSQL('DocSets')
          // window.gtvapp.docsets = await this.db.get('DocSets')
          this.$router.push('/category')
        }
      )
    } else {
      window.gtvapp.wait.c--
      this.$router.push('/category')
    }
  }
}


