export default {
  name: 'Category',
  data () {
    return {
      categories : window.gtvapp.categories,
      currentCategories: [],
      docsets: window.gtvapp.docsets,
      db: window.gtvapp.db,
      current: undefined,
      md: new window.utils.md(),
      search: {
        query: undefined
      }
    }
  },
  methods: {
    async rootCat (id) {
      if(this.categories == undefined) {
        this.categories = await this.db.getSQL('Categories')
        // this.categories = await this.db.get('Categories')
      }
      let ret
      if(this.categories !== undefined){
        ret = this.categories.filter(function(cat) {
          return cat.parent == id
        })
      } else {
        ret = []
      }
      return ret
    },
    async getDocSet(id) {
      return await this.db.getSQL('DocSets', parseInt(id))
      // return await this.db.get('DocSets', parseInt(id))
    },
    async getContent () {
      let root = this.$route.params.id
      this.currentCategories = await this.rootCat(root)
      if(root !== undefined && root !== 'root'){
        this.current = await this.db.getSQL('Categories', parseInt(root))
        // this.current = await this.db.get('Categories', parseInt(root))
        if(this.current !== undefined) {
          this.current.popDocsets = []
          this.current.docsets.forEach(async (id) => {
            let d = await this.db.getSQL('DocSets', id, true)
            // let d = await this.db.get('DocSets', id)
            this.current.popDocsets.push(d)
            this.$forceUpdate()
          })
        }
      } else {
        this.current = undefined
      }
    }
  },
  watch: {
    '$route': async function () {
      this.getContent()
    }
  },
  async mounted () {
    this.getContent()
  }
}


